.container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    width: 500px;
    height: 500px;
    padding: 30px 20px;
    background: rgb(114, 5, 105, 0.05);

    border: 2px solid #7c7c7c;
    border-radius: 20px;
    backdrop-filter: blur(80px);
    cursor: pointer;

    -webkit-box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000, 8px 31px 13px 23px rgba(124, 124, 124, 0);
    box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000, 8px 31px 13px 23px rgba(124, 124, 124, 0);


    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
}

.wrapperInput {
    display: flex;
    flex-direction: column;
    align-items: center;

    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

}

.input {
    width: 100%;
    background: transparent;
    border: none;
    border-bottom: 1px solid #444;
    outline: none !important;
    box-shadow: none;

    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    font-size: 24px;
    font-family: Arial;
    resize: none;
    padding: 5px;
    overflow: hidden;
    box-sizing: border-box;
    height: 80px;
    min-height: 40px;

    margin-bottom: 15px;
    border: 2px solid #7c7c7c;

    text-align: center;
    color: transparent;


    border-radius: 6px !important;
    cursor: auto;

    -webkit-box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);
    -moz-box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);
    box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);
    font-weight: 600;
    font-size: 24px;

    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(180deg, #FFFFFF 21.75%, #1A3031 100%);
    caret-color: #06f8fc;
}

.input:focus {
    background: transparent;
    border: none;
    border-bottom: 1px solid #444;
    outline: none !important;
    box-shadow: none;
    border: 2px solid #7c7c7c;
    border-bottom: 2px solid #06f8fc;
    transition: all;
    transition-duration: 600ms;
    font-size: 40px;
    font-weight: 700;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(180deg, #FFFFFF 21.75%, #1A3031 100%);
    border-radius: 12px;

    -webkit-box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);
    -moz-box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);
    box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);

    font-weight: 600;
    font-size: 24px;
    letter-spacing: 3px;

}


.input:hover {
    background: transparent;
    border: none;
    border-bottom: 1px solid #444;
    outline: none !important;
    box-shadow: none;
    border: 2px solid #7c7c7c;
    border-bottom: 2px solid rgb(6, 248, 252);
    transition: all;
    transition-duration: 600ms;

    font-size: 30px;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    transition: all 1s;
    background-image: linear-gradient(180deg, #FFFFFF 21.75%, #1A3031 100%);
    border-radius: 12px;

    -webkit-box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);
    -moz-box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);
    box-shadow: inset -1px 2px 13px -1px rgb(252, 6, 174, 0.5);

    font-weight: 600;
    font-size: 24px;
}

.input::placeholder{
    font-weight: 300;
    font-size: 20px;
    color: #08b5ff;
    letter-spacing: 3px;
}
.input:not{
    letter-spacing: 3px;
}





.inputLabel {

    font-weight: 600;
    font-size: 30px;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(180deg, #FFFFFF 21.75%, #1A3031 100%);

}

.autx_btn {
    cursor: pointer;
    border-color: #7c7c7c;
    background: linear-gradient(top, rgba(38, 38, 38, 0.8), #e6e6e6 25%, #ffffff 38%, #c5c5c5 63%, #f7f7f7 87%, rgba(38, 38, 38, 0.8));
    background: -webkit-linear-gradient(top, rgba(38, 38, 38, 0.5), #e6e6e6 25%, #ffffff 38%, rgba(0, 0, 0, 0.25) 63%, #e6e6e6 87%, rgba(38, 38, 38, 0.4));

    box-sizing: border-box;
    width: 200px;
    height: 60px;
    border-radius: 50px 0px 50px 0px;


    font-weight: 600;
    font-size: 24px;

    color: #52f6e2;
    letter-spacing: 3px;
    -webkit-text-shadow: 0px 0px 6px rgba(0,  0,  0, 0.42);
    -moz-text-shadow: 0px 0px 6px rgba(0,  0,  0, 0.42);
    text-shadow: 0px 0px 6px rgba(0,  0,  0, 0.42);

    background: linear-gradient(top, rgba(38, 38, 38, 0.8), #e6e6e6 25%, #ffffff 38%, #c5c5c5 63%, #f7f7f7 87%, rgba(38, 38, 38, 0.8));
    background: -webkit-linear-gradient(top, rgba(38, 38, 38, 0.5), #e6e6e6 25%, #ffffff 38%, rgba(0, 0, 0, 0.25) 63%, #e6e6e6 87%, rgba(38, 38, 38, 0.4));

    border: 2px solid #7c7c7c;

}
.autx_btn:hover{
    box-shadow: none !important;
    outline: none !important;

    background: linear-gradient(180deg, #e6e6e6 0%, rgba(0, 0, 0, 0.25) 49%, rgba(38, 38, 38, 0.6) 51%, rgba(0, 0, 0, 0.25) 100%);

    border: 2px solid #151515;
   
    font-weight: 600;
    font-size: 24px;
}